.godfather-entry {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10; }
  .godfather-entry .godfather-animation {
    animation: godfather-enter 200ms ease-out;
    line-height: 1.42;
    border-radius: 4px;
    box-shadow: 0 15px 25px -5px rgba(0, 0, 0, 0.4); }
  .godfather-entry .godfather-container {
    display: flex; }
  .godfather-entry .godfather-inner-container {
    padding: 1em;
    max-width: 300px; }
  .godfather-entry .godfather-actions {
    margin-top: 15px;
    display: flex;
    justify-content: space-between; }
  .godfather-entry button {
    font-size: 0.75em;
    cursor: pointer;
    padding: 5px 15px;
    color: inherit;
    background: rgba(255, 255, 255, 0.1);
    border: none;
    color: inherit;
    border-radius: 2px;
    transition: background 100ms ease-out; }
    .godfather-entry button:hover {
      background: rgba(255, 255, 255, 0.2); }
  .godfather-entry .godfather-image {
    background-position: center;
    background-size: 75% 75%;
    background-repeat: no-repeat;
    background-color: rgba(255, 255, 255, 0.05);
    width: 125px;
    position: relative; }
  .godfather-entry .godfather-image-loader:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -10px;
    margin-top: -10px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid rgba(255, 255, 255, 0.5);
    border-top-color: transparent;
    animation: spin 500ms linear infinite; }

@keyframes spin {
  100% {
    transform: rotate(1turn); } }
  .godfather-entry .godfather-title {
    font-weight: bold; }
  .godfather-entry hr {
    border: none;
    border-top: 1px solid currentColor;
    opacity: 0.2;
    margin: 0.5em 0; }
  .godfather-entry .tooltip-arrow {
    width: 0;
    height: 0;
    border-style: solid;
    position: absolute;
    margin: 6px;
    border-color: currentColor; }
  .godfather-entry[x-placement^='top'] {
    margin-bottom: 6px; }
    .godfather-entry[x-placement^='top'] .tooltip-arrow {
      border-width: 6px 6px 0 6px;
      border-left-color: transparent !important;
      border-right-color: transparent !important;
      border-bottom-color: transparent !important;
      bottom: -6px;
      left: calc(50% - $tooltip-size);
      margin-top: 0;
      margin-bottom: 0; }
  .godfather-entry[x-placement^='bottom'] {
    margin-top: 6px; }
    .godfather-entry[x-placement^='bottom'] .tooltip-arrow {
      border-width: 0 6px 6px 6px;
      border-left-color: transparent !important;
      border-right-color: transparent !important;
      border-top-color: transparent !important;
      top: -6px;
      left: calc(50% - $tooltip-size);
      margin-top: 0;
      margin-bottom: 0; }
  .godfather-entry[x-placement^='right'] {
    margin-left: 6px; }
    .godfather-entry[x-placement^='right'] .tooltip-arrow {
      border-width: 6px 6px 6px 0;
      border-left-color: transparent !important;
      border-top-color: transparent !important;
      border-bottom-color: transparent !important;
      left: -6px;
      top: calc(50% - $tooltip-size);
      margin-left: 0;
      margin-right: 0; }
  .godfather-entry[x-placement^='left'] {
    margin-right: 6px; }
    .godfather-entry[x-placement^='left'] .tooltip-arrow {
      border-width: 6px 0 6px 6px;
      border-top-color: transparent !important;
      border-right-color: transparent !important;
      border-bottom-color: transparent !important;
      right: -6px;
      top: calc(50% - $tooltip-size);
      margin-left: 0;
      margin-right: 0; }

.godfather-hint {
  cursor: pointer;
  position: absolute;
  opacity: 0.9;
  top: -5px;
  right: -5px;
  border-radius: 50%;
  background: currentColor;
  width: 20px;
  height: 20px; }
  .godfather-hint:before {
    content: '?';
    display: block;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    position: relative;
    font-size: 13px;
    z-index: 1;
    color: white;
    text-align: center;
    line-height: 20px; }
  .godfather-hint:after {
    content: '';
    flex-shrink: 0;
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: currentColor;
    animation: pulse 2s infinite ease-out; }

@keyframes pulse {
  0% {
    transform: scale(1); }
  100% {
    transform: scale(1.5);
    opacity: 0; } }

.godfather-overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 8; }

@keyframes godfather-enter {
  0% {
    opacity: 0;
    transform: translateY(10px); } }
